body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.psg3:hover {
  box-shadow: 1px 3px 5px;
  animation-iteration-count: infinite;
  position: absolute;
  animation-name: example;
  animation-duration: 2s;
}

@keyframes example {
  0% {
    border-left:  5px solid rgb(214, 13, 217);
    border-top: 0px solid rgb(214, 13, 217);
    border-bottom: 0px solid rgb(161, 56, 163);
    border-right: 0px solid rgb(214, 13, 217);
  }

  25% {
    border-left: 0px solid rgb(214, 13, 217);
    border-top:  5px solid rgb(214, 13, 217);
    border-bottom: 0px solid rgb(214, 13, 217);
    border-right: 0px solid rgb(117, 99, 118);
  }

  50% {
    border-left: 0px solid rgb(214, 13, 217);
    border-top: 0px solid rgb(214, 13, 217);
    border-bottom: 0px solid rgb(214, 13, 217);
    border-right:  5px solid rgb(214, 13, 217);
  }

  75% {
    border-left: 0px solid rgb(214, 13, 217);
    border-top: 0px solid rgb(214, 13, 217);
    border-bottom:  5px solid rgb(214, 13, 217);
    border-right: 0px solid rgb(214, 13, 217);
  }

  100% {
    border-left:  5px solid rgb(214, 13, 217);
    border-top:  5px solid rgb(214, 13, 217);
    border-bottom:  5px solid rgb(214, 13, 217);
    border-right:  5px solid rgb(214, 13, 217);
  }
}

.psg1 {
  background-color: #6DA5C0;
}

@media (max-width: 750px) {
  .psg2 {
    margin: 2rem;
  }

}

.psg {
  background-color: rgb(13, 13, 14);
  color: wheat;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}